<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <div class="slds-form-element content-section">
        <p class="slds-text-body_regular content-section__label">After a chat conversation is marked as done, keep chat messages for&nbsp;&nbsp;
          <span
            class="content-section__picklist"
          >
            <ld-picklist
              :options="aggregatedDays"
              :value="selectedAggregatedDay"
              @change="selectAggregatedDay"
            >
            </ld-picklist>
          </span>
          before they are aggregated.
        </p>
        <p class="slds-text-body_regular">Format timestamps using&nbsp;&nbsp;
          <span
            class="content-section__picklist--timezone"
          >
            <ld-picklist
              :options="aggregatedTimezones"
              :value="selectedAggregatedTimezone"
              :width="540"
              menu-position="right"
              @change="selectAggregatedTimezone"
            >
            </ld-picklist>
          </span>
          time zone when chat messages are being aggregated.
        </p>
      </div>
      <footer class="slds-modal__footer">
        <button class="slds-button slds-button_neutral" :disabled="!hasChanged || isSaving" @click="onClickCancel">Cancel</button>
        <button class="slds-button slds-button_brand" :disabled="!hasChanged || isSaving" @click="onClickSave">Save</button>
      </footer>
    </div>
  </right-page>
</template>

<script>
import { LdPicklist } from 'charket-ui/dist/ld-picklist'
import { UserService, CharketSettingsService } from '../service'

export default {
  name: 'ChatDataSettings',
  components: {
    LdPicklist
  },
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Chat Data Settings'
      },
      aggregatedDays: [
        { label: '15 Days', value: '15' },
        { label: '30 Days', value: '30' },
        { label: '90 Days', value: '90' },
        { label: '180 Days', value: '180' },
        { label: '1 Year', value: '365' },
        { label: 'Infinitely', value: '-1' }
      ],
      selectedAggregatedDay: '',
      aggregatedTimezones: [],
      selectedAggregatedTimezone: '',
      chatDataSettings: {},
      isSaving: false
    }
  },
  computed: {
    hasChanged () {
      return `${this.chatDataSettings.DaysBeforeTranscriptItemArchived}` !== this.selectedAggregatedDay ||
             this.chatDataSettings.ChatFormatTimeZoneSidKey !== this.selectedAggregatedTimezone
    }
  },
  created () {
    this.fetchSettings()
  },
  methods: {
    async fetchSettings () {
      this.$bus.emit('show-loading')
      await this.$try(async () => {
        const [ userTimezones, chatDataSettings ] = await Promise.all([
          UserService.getUserTimeZones(),
          CharketSettingsService.getChatDataSettings()
        ])
        userTimezones.unshift({ Name: 'Current User', Value: 'Current User' })
        this.aggregatedTimezones = userTimezones.map(timezone => ({ label: timezone.Name, value: timezone.Value }))
        this.selectedAggregatedDay = `${chatDataSettings.DaysBeforeTranscriptItemArchived || 15}`
        this.selectedAggregatedTimezone = chatDataSettings.ChatFormatTimeZoneSidKey || 'America/Chicago'
        this.chatDataSettings = chatDataSettings
      })
      this.$bus.emit('hide-loading')
    },
    selectAggregatedDay (item) {
      this.selectedAggregatedDay = item.value
    },
    selectAggregatedTimezone (item) {
      this.selectedAggregatedTimezone = item.value
    },
    onClickCancel () {
      this.selectedAggregatedDay = `${this.chatDataSettings.DaysBeforeTranscriptItemArchived}`
      this.selectedAggregatedTimezone = this.chatDataSettings.ChatFormatTimeZoneSidKey
    },
    async onClickSave () {
      this.isSaving = true
      await this.$try(async () => {
        const settings = {
          DaysBeforeTranscriptItemArchived: parseInt(this.selectedAggregatedDay),
          ChatFormatTimeZoneSidKey: this.selectedAggregatedTimezone
        }
        await CharketSettingsService.saveChatDataSettings(settings)
        this.chatDataSettings = settings
        this.$bus.emit('success', { content: 'Chat Data Settings was saved.', type: 'success' })
      })
      this.isSaving = false
    }
  }
}
</script>

<style lang="scss" scoped>
  .content-section {
    padding: 20px 30px;
    &__label {
      padding-bottom: 12px;
    }
    &__picklist {
      display: inline-block;
      padding-right: 8px;
    }
    &__picklist--timezone {
      display: inline-block;
      padding-right: 8px;
      width: 300px;
    }
  }
</style>
